.image-uploader-preview {
	max-width: 150px;
}

html,
body,
#root {
	height: 100%;
}

main {
	max-width: 100%;
	overflow-x: hidden;
}

.main-container {
	min-height: calc(100% - 10rem);
	overflow: hidden;
}

.top-heading {
	width: 100%;
	padding: 6rem 0 2rem 0;
	&.selected {
		border: 2px solid red;
	}
	div {
		padding: 0 1rem;
	}
	text-align: center;
	font-family: 'Libre Baskerville', serif;
	font-weight: 700;
	span {
		text-transform: lowercase;
		font-weight: 300;
	}
}

.desktop {
	.top-heading {
		padding: 11rem 0 4rem 0;
	}
}

.tablet {
	.top-heading {
		padding: 8rem 0 3rem 0;
	}
	section.text {
		max-width: 500px;
	}
}

main {
	margin: 0 auto;
}

body {
	padding: 0;
	margin: 0;
	font-family: 'Roboto Condensed', 'HelveticaNeue-Light', 'Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial,
		'Lucida Grande', sans-serif;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.5;
	color: white;
	background: black;
}

a {
	color: white;
	text-decoration: none;
	&:visited {
		color: white;
	}
}

section.text a {
	text-decoration: none;
	color: #4a91e4;
	&[aria-current='page'] {
		&:hover {
			color: white;
		}
	}
	&:hover {
		cursor: pointer;
		color: #1f88ff;
	}
}

section.text {
	// max-width: 800px;
	margin: 0 auto;
	max-width: 800px;
	padding: 1rem;
}

button {
	background: #444;
	padding: 1em;
	font-family: 'Roboto Condensed', 'HelveticaNeue-Light', 'Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial,
		'Lucida Grande', sans-serif;
	text-transform: uppercase;
	font-size: 16px;
	color: white;
	border: none;
	margin: .2em;
	transition: background .3s ease-in-out;
	&.enabled {
		background: red;
	}
	&:hover {
		cursor: pointer;
		background: #555;
	}
}

.spotify-iframe {
	margin-bottom: 2rem;
}

span.arrow {
	position: relative;
	top: .05rem;
	font-size: 1.5em;
	font-weight: 400;
}

.button {
	color: white;
	padding: 3px 10px 2px 10px;
	border: 2px solid white;
	border-radius: .4rem;
	font-weight: 600;
	font-size: .8rem;
	text-transform: uppercase;
}

.background-image {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
}
// 	width: 100%;
// 	height: 50vw;
// 	background-size: cover;
// 	background-position: center;
// 	background-repeat: no-repeat;

.page-container {
	margin: 0 auto;
	padding: 0;
	width: 100%;
	max-width: 2560px;
	.wrapper {
		height: 100%;
		width: 100%;
	}
	.video-link {
		&:hover {
			cursor: pointer;
		}
		div {
			height: 100%;
		}
		.icon {
			color: white;
			position: absolute;
			left: 0;
			width: 100%;
			top: 50%;
			height: 50px;
			margin-top: -25px;
			text-align: center;
		}
	}
}
